/* debug */
[debug="outline"] * {
  background: rgba(255, 0, 0, 0.1);
  box-shadow: 0 0 0 1px red;
  outline: 1px solid red;
}

@media (min-width: 320px) {
  [debug="screen"] {
    position: absolute;
    top: 0;
    left: 0;
    padding: 3px 8px;
    color: white;
    background-color: black;
    z-index: 999999;
  }
  [debug="screen"]::after {
